import React, { useState, useContext } from 'react';
import {
  View,
  TextInput,
  TouchableOpacity,
  Text,
  StyleSheet,
  Alert,
  ActivityIndicator,
  Platform,
} from 'react-native';
import { authAPI } from '../services/api';
import AsyncStorage from '@react-native-async-storage/async-storage';
import { CommonActions, useNavigation } from '@react-navigation/native';
import { AuthContext } from '../navigation';
import HCaptcha from '@hcaptcha/react-hcaptcha';

const styles = StyleSheet.create({
  container: {
    flex: 1,
    justifyContent: 'center',
    padding: 20,
    backgroundColor: '#fff',
  },
  title: {
    fontSize: 24,
    fontWeight: 'bold',
    marginBottom: 30,
    textAlign: 'center',
  },
  input: {
    height: 50,
    borderWidth: 1,
    borderColor: '#ddd',
    borderRadius: 8,
    paddingHorizontal: 15,
    marginBottom: 15,
    fontSize: 16,
    backgroundColor: '#fff',
    ...(Platform.OS === 'web' && {
      outlineStyle: 'none',
      ':focus': {
        borderColor: '#007AFF',
      },
    }),
  },
  buttonContainer: {
    pointerEvents: 'auto',
    marginBottom: 15,
  },
  button: {
    height: 50,
    backgroundColor: '#007AFF',
    borderRadius: 8,
    justifyContent: 'center',
    alignItems: 'center',
    ...(Platform.OS === 'web' && {
      cursor: 'pointer',
      ':hover': {
        backgroundColor: '#0056b3',
      },
    }),
  },
  buttonDisabled: {
    backgroundColor: '#ccc',
  },
  buttonText: {
    color: 'white',
    fontSize: 16,
    fontWeight: '600',
  },
  switchText: {
    color: '#007AFF',
    textAlign: 'center',
    marginTop: 15,
    fontSize: 16,
  },
  stepText: {
    fontSize: 18,
    marginBottom: 15,
    textAlign: 'center',
  },
});

const LoginScreen = () => {
  const navigation = useNavigation();
  const { setIsLoggedIn } = useContext(AuthContext);
  const [isRegistering, setIsRegistering] = useState(false);
  const [registrationStep, setRegistrationStep] = useState(1);
  
  // Login state
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [loading, setLoading] = useState(false);

  // Registration state
  const [name, setName] = useState('');
  const [username, setUsername] = useState('');
  const [regEmail, setRegEmail] = useState('');
  const [regPassword, setRegPassword] = useState('');
  const [captchaToken, setCaptchaToken] = useState('');

  const handleLogin = async () => {
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (!email) {
      Alert.alert('Ошибка', 'Введите почту');
      return;
    }
    if (!emailRegex.test(email)) {
      Alert.alert('Ошибка', 'Введите корректный email адрес');
      return;
    }
    if (!password) {
      Alert.alert('Ошибка', 'Введите пароль');
      return;
    }

    try {
      setLoading(true);
      const response = await authAPI.login(email.trim(), password);
      
      if (response?.status === 'success' && response?.S_KEY) {
        await AsyncStorage.setItem('S-KEY', response.S_KEY);
        setIsLoggedIn(true);
      } else {
        Alert.alert('Ошибка', 'Неверный email или пароль');
      }
    } catch (error) {
      console.error('Login error:', error);
      Alert.alert('Ошибка', 'Что-то пошло не так');
    } finally {
      setLoading(false);
    }
  };

  const handleRegister = async () => {
    try {
      setLoading(true);
      const response = await fetch('https://api.elemsocial.com/auth/register', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          name,
          username,
          email: regEmail,
          password: regPassword,
          accept: true,
          h_captcha: captchaToken,
        }),
      });

      const data = await response.json();
      
      if (data.status === 'success') {
        await AsyncStorage.setItem('S-KEY', data.S_KEY);
        setIsLoggedIn(true);
        Alert.alert('Успешно', 'Регистрация завершена');
      } else {
        Alert.alert('Ошибка', 'Не удалось зарегистрироваться');
      }
    } catch (error) {
      console.error('Registration error:', error);
      Alert.alert('Ошибка', 'Что-то пошло не так');
    } finally {
      setLoading(false);
    }
  };

  const onCaptchaVerify = (token: string) => {
    setCaptchaToken(token);
  };

  const handleNextStep = () => {
    if (registrationStep === 1 && !name) {
      Alert.alert('Ошибка', 'Введите имя');
      return;
    }
    if (registrationStep === 2 && !username) {
      Alert.alert('Ошибка', 'Введите имя пользователя');
      return;
    }
    if (registrationStep === 3 && !regEmail) {
      Alert.alert('Ошибка', 'Введите email');
      return;
    }
    if (registrationStep === 4 && !regPassword) {
      Alert.alert('Ошибка', 'Введите пароль');
      return;
    }
    if (registrationStep < 5) {
      setRegistrationStep(registrationStep + 1);
    } else if (captchaToken) {
      handleRegister();
    }
  };

  const renderRegistrationStep = () => {
    switch (registrationStep) {
      case 1:
        return (
          <>
            <Text style={styles.stepText}>Введите ваше имя</Text>
            <TextInput
              style={styles.input}
              placeholder="Имя"
              value={name}
              onChangeText={setName}
              editable={!loading}
            />
          </>
        );
      case 2:
        return (
          <>
            <Text style={styles.stepText}>Придумайте имя пользователя</Text>
            <TextInput
              style={styles.input}
              placeholder="Имя пользователя"
              value={username}
              onChangeText={setUsername}
              editable={!loading}
              autoCapitalize="none"
            />
          </>
        );
      case 3:
        return (
          <>
            <Text style={styles.stepText}>Введите email</Text>
            <TextInput
              style={styles.input}
              placeholder="Email"
              value={regEmail}
              onChangeText={setRegEmail}
              editable={!loading}
              keyboardType="email-address"
              autoCapitalize="none"
            />
          </>
        );
      case 4:
        return (
          <>
            <Text style={styles.stepText}>Придумайте пароль</Text>
            <TextInput
              style={styles.input}
              placeholder="Пароль"
              value={regPassword}
              onChangeText={setRegPassword}
              secureTextEntry
              editable={!loading}
            />
          </>
        );
      case 5:
        return (
          <>
            <Text style={styles.stepText}>Подтвердите, что вы не робот</Text>
            <HCaptcha
              sitekey="29c6b1c2-7e78-43ec-8bf8-5de49c58c54a"
              onVerify={onCaptchaVerify}
            />
          </>
        );
      default:
        return null;
    }
  };

  return (
    <View style={styles.container}>
      <Text style={styles.title}>Sigma Element Client</Text>
      
      {isRegistering ? (
        <>
          {renderRegistrationStep()}
          <View style={styles.buttonContainer}>
            <TouchableOpacity
              style={[styles.button, loading && styles.buttonDisabled]}
              onPress={handleNextStep}
              disabled={loading}
            >
              {loading ? (
                <ActivityIndicator color="white" />
              ) : (
                <Text style={styles.buttonText}>
                  {registrationStep === 5 ? 'Зарегистрироваться' : 'Далее'}
                </Text>
              )}
            </TouchableOpacity>
          </View>
          <TouchableOpacity onPress={() => {
            setIsRegistering(false);
            setRegistrationStep(1);
          }}>
            <Text style={styles.switchText}>Уже есть аккаунт? Войти</Text>
          </TouchableOpacity>
        </>
      ) : (
        <>
          <TextInput
            style={styles.input}
            placeholder="Электронная почта"
            value={email}
            onChangeText={setEmail}
            autoCapitalize="none"
            keyboardType="email-address"
            editable={!loading}
          />
          <TextInput
            style={styles.input}
            placeholder="Пароль"
            value={password}
            onChangeText={setPassword}
            secureTextEntry
            editable={!loading}
          />
          <View style={styles.buttonContainer}>
            <TouchableOpacity
              style={[styles.button, loading && styles.buttonDisabled]}
              onPress={handleLogin}
              disabled={loading}
            >
              {loading ? (
                <ActivityIndicator color="white" />
              ) : (
                <Text style={styles.buttonText}>Войти</Text>
              )}
            </TouchableOpacity>
          </View>
          <TouchableOpacity onPress={() => setIsRegistering(true)}>
            <Text style={styles.switchText}>Нет аккаунта? Зарегистрироваться</Text>
          </TouchableOpacity>
        </>
      )}
    </View>
  );
};

export default LoginScreen;
